@font-face {
    font-family: 'icon';
    src: url(/blog/static/media/iconfont.6d54219b.eot);
    src: url(/blog/static/media/iconfont.6d54219b.eot?#iefix) format('embedded-opentype'),
        url(/blog/static/media/iconfont.c63fe45c.woff2) format('woff2'),
        url(/blog/static/media/iconfont.34ee1b4e.woff) format('woff'),
        url(/blog/static/media/iconfont.e2809522.ttf) format('truetype'),
        url(/blog/static/media/iconfont.a8751e9f.svg#iconfont) format('svg');
}
.icon{
    font-family: "icon";
    min-width:14px;
    margin-right:10px;
    font-size: 14px;
    -webkit-transition: font-size 0.15s cubic-bezier(0.215, 0.61, 0.355, 1), margin 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: font-size 0.15s cubic-bezier(0.215, 0.61, 0.355, 1), margin 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}
pre{
    background-color: beige;
    color: #333;
    padding: 10px;
    border-radius: 5px;
}
.logo{
    height:80px;
    color: white;
    text-align: center;
    line-height: 80px;
    letter-spacing: 20px;
    font-size: 18px;
    font-weight: bolder;
    text-shadow: 3px 3px 8px rgb(0, 204, 255)
}
.logo::first-letter{
    font-size: 26px; 
}
.annotation{
    font-size: 12px;
}
